import img1 from '../../images/img4.png';
import img2 from '../../images/img1.png';
import img3 from '../../images/img2.png';
import img4 from '../../images/img3.png';

// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react';

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/navigation';

// import required modules
import { Pagination, Navigation } from 'swiper/modules';

import './SmallSlider.scss';

const IMAGES = [img1, img2, img3, img4];

export const SmallSlider = () => {
  return (
    <>
      <Swiper
        slidesPerView={1}
        spaceBetween={30}
        centeredSlides={true}
        loop={true}
        pagination={{
          clickable: true,
        }}
        modules={[Pagination, Navigation]}
        className="mySwiper"
      >
        {IMAGES.map((image) => (
          <SwiperSlide><img src={image} alt="" /></SwiperSlide>
        ))}
      </Swiper>
    </>
  );
};

export default SmallSlider;
